import { global } from '@storybook/global';
import { addons } from '@storybook/preview-api';
import { STORY_CHANGED } from '@storybook/core-events';

// src/preview.ts

// src/constants.ts
var ADDON_ID = "storybook/highlight";
var HIGHLIGHT_STYLE_ID = "storybookHighlight";
var HIGHLIGHT = `${ADDON_ID}/add`;
var RESET_HIGHLIGHT = `${ADDON_ID}/reset`;

// src/preview.ts
var { document } = global;
var highlightStyle = (color = "#FF4785", style = "dashed") => `
  outline: 2px ${style} ${color};
  outline-offset: 2px;
  box-shadow: 0 0 0 6px rgba(255,255,255,0.6);
`;
var highlightObject = (color) => ({
  outline: `2px dashed ${color}`,
  outlineOffset: 2,
  boxShadow: "0 0 0 6px rgba(255,255,255,0.6)"
});
var channel = addons.getChannel();
var highlight = (infos) => {
  const id = HIGHLIGHT_STYLE_ID;
  resetHighlight();
  const elements = Array.from(new Set(infos.elements));
  const sheet = document.createElement("style");
  sheet.setAttribute("id", id);
  sheet.innerHTML = elements.map(
    (target) => `${target}{
          ${highlightStyle(infos.color, infos.style)}
         }`
  ).join(" ");
  document.head.appendChild(sheet);
};
var resetHighlight = () => {
  const id = HIGHLIGHT_STYLE_ID;
  const sheetToBeRemoved = document.getElementById(id);
  if (sheetToBeRemoved) {
    sheetToBeRemoved.parentNode?.removeChild(sheetToBeRemoved);
  }
};
channel.on(STORY_CHANGED, resetHighlight);
channel.on(RESET_HIGHLIGHT, resetHighlight);
channel.on(HIGHLIGHT, highlight);

export { highlightObject, highlightStyle };
